 :root{
	--primary-color: #41C5B8;
  --panel-bg-color: #f7f7f7; 
  --panel-text-color: #333; 
 }
.wp-block-create-block-altimate-gutenberg-carousel {
	background-color: var(--primary-color);
	color: #fff;
	padding: 2px;
}

.wp-block-editor .components-panel__body {
    overflow-y: hidden;
}

.altimate-gutenberg-tabs-header button {
  margin-right: 10px;
  padding: 6px 12px;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
}
.altimate-gutenberg-tabs-header button.active {
  background-color: var(--primary-color);
  color: white;
}
.altimate-gutenberg-tab-panel{
  padding-top: 20px;
}
.inner-tab-buttons button.active {
  font-weight: bold;
}
