.agc-skin-container{
  display: flex;
  justify-content: space-around;
}
.agc-skin-container label{
  align-content: center;
}
.agc-slider-tab-button{
  display: flex;
  justify-content:space-around;
}
.agc-slider-tab-container{
  padding:20px
}
.agc-slider-tab-button button{
  padding: 6px 12px;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
}
.agc-slider-tab-button button.active {
  background-color: var(--primary-color);
  color: white;
}
.agc-slider-tab-content{
  padding-top: 20px
}
.agc-toggle-button-color .components-form-toggle.is-checked .components-form-toggle__track {
  background-color: var(--primary-color)!important; 
}
.agc-toggle-button-color .components-form-toggle.is-checked .components-form-toggle__thumb {
  border-color: var(--primary-color) !important;
}
.agc-horizontal-alignment{
  padding-bottom:20px;
}
.agc-vertical-alignment{
  padding-bottom: 20px;
}

